import React, { Suspense, lazy } from "react"
import { Router, Switch, Route } from "react-router-dom"
import { history } from "./history"
import { connect } from "react-redux"
import Spinner from "./components/@vuexy/spinner/Loading-spinner"
import { ContextLayout } from "./utility/context/Layout"

// Route-based code splitting
const analyticsDashboard = lazy(() => import("./views/dashboard/analytics/AnalyticsDashboard"))
const ecommerceDashboard = lazy(() => import("./views/dashboard/ecommerce/EcommerceDashboard"))
const group = lazy(() => import("./views/forms/form-elements/input/ArrivalForms"))
const textarea = lazy(() => import("./views/forms/form-elements/input/NewServiceType"))
const select = lazy(() => import("./views/forms/form-elements/input/NewBooking"))
const listView = lazy(() => import("./views/forms/form-elements/input/NewCustomerRegister"))
const Login = lazy(() => import("./views/pages/authentication/login/Login"))
const forgotPassword = lazy(() => import("./views/pages/authentication/ForgotPassword"))
const newjob = lazy(() => import("./views/forms/form-elements/input/AddNewJob"))
const newadmissionadd = lazy(() => import("./views/forms/form-elements/input/NewAdmissionAdd"))
const resetPassword = lazy(() => import("./views/pages/authentication/ResetPassword"))
const register = lazy(() => import("./views/pages/authentication/register/Register"))
const newuniver = lazy(() => import("./views/forms/form-elements/input/AddNeUniBoard"))
const addnewtut = lazy(() => import("./views/forms/form-elements/input/AddNewTutorial"))
const addnewtesting = lazy(() => import("./views/forms/form-elements/input/AddNewTestingService"))
const addnewdepartment = lazy(() => import("./views/forms/form-elements/input/AddNewDepartment"))
const addnewcategory = lazy(() => import("./views/forms/form-elements/input/AddNewCategory"))
const addnewbookingtype = lazy(() => import("./views/forms/form-elements/input/AddNewBookingType"))
const addnewordertype = lazy(() => import("./views/forms/form-elements/input/AddNewOrderType"))
const addnewcoriertype = lazy(() => import("./views/forms/form-elements/input/AddNewCourierCompany"))
const carourierbulkbookingform = lazy(() => import("./views/forms/form-elements/input/CourierBulkBookingForm"))
const quickbookingform = lazy(() => import("./views/forms/form-elements/input/QuickBookingForm"))
const accountopeningform = lazy(() => import("./views/forms/form-elements/input/AccountOpeningRequest"))
const newoffice = lazy(() => import("./views/forms/form-elements/input/AddNewOffice"))
const quickbookingforms = lazy(() => import("./views/forms/form-elements/input/QuickBookingForms"))
const userregisterforms = lazy(() => import("./views/forms/form-elements/input/UserRegistrationForm"))
const addnewposition = lazy(() => import("./views/forms/form-elements/input/AddNewPositionRole"))
const addnewcompany = lazy(() => import("./views/forms/form-elements/input/AddNewCompany"))
const addbookingdescriptionFeilds = lazy(() => import("./views/forms/form-elements/input/AddBookingDescriptionFeilds"))
const newbookingType = lazy(() => import("./views/forms/form-elements/input/NewBookingType"))
const allbookinglist = lazy(() => import("./views/forms/form-elements/input/AllBookingList"))
const newbookingjob = lazy(() => import("./views/forms/form-elements/input/NewBookingJob"))
const newbbookingEducation = lazy(() => import("./views/forms/form-elements/input/NewBookingEducation"))
const gmailemaillogin = lazy(() => import("./views/forms/form-elements/input/Email"))
const chatbox = lazy(() => import("./views/apps/chat/Chat"))
const todobox = lazy(() => import("./views/apps/todo/Todo"))
const calenderbox = lazy(() => import("./views/apps/calendar/Calendar"))

// Set Layout and Component Using App Route
const RouteConfig = ({ component: Component, fullLayout, ...rest }) => (
  <Route
    {...rest}
    render={props => {
      return (
        <ContextLayout.Consumer>
          {context => {
            let LayoutTag =
              fullLayout === true
                ? context.fullLayout
                : context.state.activeLayout === "horizontal"
                  ? context.horizontalLayout
                  : context.VerticalLayout
            return (
              <LayoutTag {...props} permission={props.user}>
                <Suspense fallback={<Spinner />}>
                  <Component {...props} />
                </Suspense>
              </LayoutTag>
            )
          }}
        </ContextLayout.Consumer>
      )
    }}
  />
)
const mapStateToProps = state => {
  return {
    user: state.auth.login.userRole
  }
}

const AppRoute = connect(mapStateToProps)(RouteConfig)

class AppRouter extends React.Component {
  render() {
    return (
      // Set the directory path if you are deploying in sub-folder
      <Router history={history}>
        <Switch>
          <AppRoute exact path="/" component={analyticsDashboard} />
          <AppRoute path="/ecommerce-dashboard" component={ecommerceDashboard} />
          <AppRoute path="/forms/elements/newCustomerRegister" component={listView} />
          <AppRoute path="/forms/NewBooking" component={select} />
          <AppRoute path="/forms/elements/arriavleForms" component={group} />
          <AppRoute path="/forms/newService" component={textarea} />
          <AppRoute path="/pages/login" component={Login} fullLayout />
          <AppRoute path="/forms/newuni" component={newuniver} />
          <AppRoute path="/pages/register" component={register} fullLayout />
          <AppRoute path="/AddNewJob" component={newjob} />
          <AppRoute path="/addnewtut" component={addnewtut} />
          <AppRoute path="/addnewtesting" component={addnewtesting} />
          <AppRoute path="/addnewdepartment" component={addnewdepartment} />
          <AppRoute path="/addnewcategory" component={addnewcategory} />
          <AppRoute path="/addnewbookingtype" component={addnewbookingtype} />
          <AppRoute path="/addnewordertype" component={addnewordertype} />
          <AppRoute path="/addnewcoriertype" component={addnewcoriertype} />
          <AppRoute path="/addnewposition" component={addnewposition} />
          <AppRoute path="/carourierbulkbookingform" component={carourierbulkbookingform} />
          <AppRoute path="/quickbookingform" component={quickbookingform} />
          <AppRoute path="/addbookingdescriptionfeilds" component={addbookingdescriptionFeilds} />
          <AppRoute path="/accountopeningform" component={accountopeningform} />
          <AppRoute path="/newbookingjob" component={newbookingjob} />
          <AppRoute path="/allbookinglist" component={allbookinglist} />
          <AppRoute path="/addnewcompany" component={addnewcompany} />
          <AppRoute path="/newadmissionadd" component={newadmissionadd} />
          <AppRoute path="/gmailemaillogin" component={gmailemaillogin} />
          <AppRoute path="/chatbox" component={chatbox} />
          <AppRoute path="/todobox" component={todobox} />
          <AppRoute path="/calenderbox" component={calenderbox} />
          <AppRoute path="/newbbookingEducation" component={newbbookingEducation} />
          <AppRoute path="/newoffice" component={newoffice} />
          <AppRoute path="/newbookingType" component={newbookingType} />
          <AppRouter path="/quickbookingforms" component={quickbookingforms} />
          <AppRouter path="/userregisterforms" component={userregisterforms} />
          <AppRoute path="/pages/forgot-password" component={forgotPassword}
            fullLayout />
          <AppRoute
            path="/pages/reset-password"
            component={resetPassword}
            fullLayout
          />
        </Switch>
      </Router>
    )
  }
}

export default AppRouter
